<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      <van-cell-group>
        <van-cell :title="businessNameList[0] ? businessNameList[0].dictValue : ''" />

        <van-row v-for="item in list1">
          <van-col span="4">{{item.contactName}}</van-col>
          <van-col span="8">{{item.phone}}</van-col>
          <van-col span="12">{{item.email}}</van-col>
        </van-row>
      </van-cell-group>

      <van-cell-group>
        <van-cell :title="businessNameList[1] ? businessNameList[1].dictValue : ''" />

        <van-row v-for="item in list2">
          <van-col span="4">{{item.contactName}}</van-col>
          <van-col span="8">{{item.phone}}</van-col>
          <van-col span="12">{{item.email}}</van-col>
        </van-row>
      </van-cell-group>

      <van-cell-group>
        <van-cell :title="businessNameList[2] ? businessNameList[2].dictValue : ''" />

        <van-row v-for="item in list3">
          <van-col span="4">{{item.contactName}}</van-col>
          <van-col span="8">{{item.phone}}</van-col>
          <van-col span="12" v-if="item.email">{{item.email}}</van-col>
        </van-row>
      </van-cell-group>

    </van-pull-refresh>

  </div>
</template>

<script>
  export default {
    name: "linked",
    data(){
      return {
        title: '业务人员联系方式',
        isLoading: false,
        list: [1, 2, 3],
        list1: [], // 交易咨询列表
        list2: [], // 代理清算业务列表
        list3: [], // 资产托管业务列表
        businessNameList: [] // 业务名称列表
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        this.getData(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
        })
      },
      getData(fn){
        this.$http.all([
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/dict/getDictByParentCode.json?parentDictCode=contact&authorization=',
              type:'get'
            }),
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/contact/getContactList.json?authorization=',
              type:'get'
            })
        ]).then(res => {
          let businessName = res[0];
          let list = res[1];
          if(businessName.data.success && list.data.success){
            this.businessNameList = JSON.parse(businessName.data.data).data;
            let data = JSON.parse(list.data.data).data;
            this.list1 = data['contact-01'];
            this.list2 = data['contact-02'];
            this.list3 = data['contact-03'];
            fn && fn();
          }else{
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          }
        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        });
      },
    },
    mounted() {
      this.getData();
    }
  }
</script>

<style scoped>

  .van-pull-refresh {
    margin-top: 46px;
    background: rgba(247, 248, 250, 1);
  }
  >>>.van-pull-refresh__track {
    min-height: calc(100vh - 56px);
  }

  .van-cell-group {
    margin-top: 10px;
    padding: 10px 15px 0;
  }
  >>>.van-cell-group [class*=van-hairline]::after {
    border: none;
  }
  .van-cell-group .van-cell {
    padding: 0;
    font-size: 15px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(32, 32, 32, 1);
    line-height: 21px;
  }
  .van-cell-group .van-cell:not(:last-child)::after {
    display: none;
  }
  .van-row {
    font-size: 13px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(155, 155, 155, 1);
    line-height: 18.5px;
    padding: 10px 0;
    border-bottom: 1px solid rgba(240, 240, 240, 1);
  }
  .van-row .van-col {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .van-row:last-child {
    border: none;
  }

</style>



